<template>
  <div class="login">
    <div class="username">
      <el-input v-model="username" placeholder="请输入用户名"></el-input>
    </div>
    <div class="password">
       <el-input type="password" v-model="password" placeholder="请输入用户名密码"></el-input>
    </div>
    <div align="right">
      <el-button type="primary" @click="login">登录</el-button>
    </div>
  </div>
</template>

<script>

export default {
  name: 'Home',
  components: {
  },
  data() {
    return {
      username: '',
      password: '',
    };
  },
  mounted() {
    if (localStorage.getItem('login')) {
      this.$router.push({
        path: '/home',
      });
    }
  },
  methods: {
    login() {
      if (this.username === 'test' && this.password === 'test') {
        this.$router.push({
          path: '/home',
        });
        localStorage.setItem('login', 'logined');
      } else {
        this.$message.warning('请输入正确的账号密码');
      }
    },
  },
};
</script>
<style lang="less" scoped>
  .login {
    height: 300px;
    width: 400px;
    margin: 0 auto;
    text-align: center;
    margin-top: 100px;
  }
  .password {
    margin: 50px auto;
  }
</style>
