<template>
  <div class="home">
    <h1>今日美业收银系统</h1>
    <div class="title">
      <div class="id">{{streamId}}</div>
      <div class="date">
        {{new Date().getFullYear()}}年{{new Date().getMonth()}}月{{new Date().getDay()}}日
      </div>
    </div>
    <div class="main">
      <div class="left">
        <div
          class="left-item"
          v-for="item in pros"
          :key="item.title"
        >
          <div>{{item.title}}</div>
          <div>
            <el-input v-model="item.price" placeholder="请输入价格"></el-input>
          </div>
        </div>
        <div class="left-item">
          <div>
            <el-button type="primary" @click="addItem" style="margin-top: 10px;" align="left">
              增加
            </el-button>
          </div>
          <div>
            <el-input v-model="title" placeholder="请输入项目"></el-input>
          </div>
        </div>
      </div>
      <div class="right">
        <div class="left-item">
          <div>
            <el-button type="primary" @click="searchVip" align="left">
              查找
            </el-button>
          </div>
          <div>
            <el-input v-model="uid" placeholder="请输入客户号"></el-input>
          </div>
        </div>
        <div class="left-item">
          <div>
            <el-select v-model="value" placeholder="请选择">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
          <div>
            <el-radio v-model="radio" label="1">指定</el-radio>
            <el-radio v-model="radio" label="2">非指定</el-radio>
          </div>
        </div>

        <div class="left-item">
          <div>
            付款方式
          </div>
          <div>
            <el-radio v-model="chekout" label="1">支付宝</el-radio>
            <el-radio v-model="chekout" label="2">微信</el-radio>
            <el-radio v-model="chekout" label="3">现金</el-radio>
          </div>
        </div>

        <div class="left-item">
          <div>
            余额
          </div>
          <div>
            <el-input v-model="restmoney" placeholder="余额"></el-input>
          </div>
        </div>

        <div class="left-item">
          <div>
            收银员
          </div>
          <div>
            <el-select v-model="name" placeholder="请选择">
              <el-option
                v-for="item in staffus"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
        </div>

        <div class="sub-btn">
          <el-button type="primary" @click="doSubBtn" align="right">
              确认点单
            </el-button>
        </div>

      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'Home',
  components: {
  },
  data() {
    return {
      streamId: 10000,
      pros: [
        { title: '洗吹', price: '' },
        { title: '洗剪吹', price: '' },
        { title: '单剪', price: '' },
        { title: '儿童剪', price: '' },
        { title: '老人剪', price: '' },
        { title: '焗油', price: '' },
        { title: '护理', price: '' },
        { title: '直发', price: '' },
        { title: '接发', price: '' },
        { title: '毛巾', price: '' },
        { title: '其他', price: '' },
      ],
      title: '',
      uid: '',
      options: [{
        value: '高级发型师',
        label: '高级发型师',
      }, {
        value: '中级发型师',
        label: '中级发型师',
      }, {
        value: '助理级发型师',
        label: '助理级发型师',
      }, {
        value: '资深发型师',
        label: '资深发型师',
      }, {
        value: '总监发型师',
        label: '总监发型师',
      }],
      staffus: [{
        value: '李四',
        label: '李四',
      }, {
        value: '张三',
        label: '张三',
      }],
      value: '',
      radio: '1',
      chekout: '1',
      restmoney: '500',
      name: '',
    };
  },
  beforeCreate() {
    if (!localStorage.getItem('login')) {
      this.$router.push({
        path: '/',
      });
    }
  },
  mounted() {
    setTimeout(() => {
      localStorage.removeItem('login');
      this.$router.push({
        path: '/',
      });
    }, 1000 * 60 * 5);
  },
  methods: {
    addItem() {
      if (!this.title) {
        this.$message.warning('请输入项目名称');
        return;
      }
      const tempObj = {
        title: this.title,
        price: '',
      };
      this.pros.push(tempObj);
    },
    searchVip() {
      if (!this.uid) {
        this.$message.warning('请输入客户号');
        return;
      }
      this.$router.push({
        path: '/about',
      });
    },
    doSubBtn() {
      this.$confirm('确定点单？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      }).then(() => {
        this.streamId += 1;
        // eslint-disable-next-line
        this.pros.forEach(item => {
        // eslint-disable-next-line
          item.price = '';
        });
        this.title = '';
        this.uid = '';
        this.value = '';
        this.radio = '1';
        this.chekout = '1';
        this.restmoney = '500';
        this.name = '';
      }).catch(() => {
      });
    },
  },
};
</script>

<style lang="less" scoped>
.home {
  width: 50%;
  margin: 0 auto;
}
.title, .main{
  display: flex;
  justify-content: space-between;
}
.left-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
  div:nth-child(1) {
    margin-right: 5px;
  }
}
.right {
  overflow: hidden;
}
.sub-btn {
  float: right;
  margin-top: 40px;
}
</style>
